@mixin animate-anchores {
  text-decoration: none;
  background: linear-gradient(
    to right,
    #d3c3c3dc 50%,
    #794244ee 25%,
    #c2363dee
  );
  background-size: 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.5s ease-in-out;
  &:hover {
    animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
    background-position: -100%;
    transition: 0.5s ease-in-out;
  }
}

@mixin centralize {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flex-alignItemCenter-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@mixin servicesCards {
  letter-spacing: 3px;
  line-height: 1.4rem;
  color: #d3c8c8cc;
  font-weight: lighter;
}

@mixin servicesCardsRows {
  width: 80%;
  display: flex;
  justify-content: space-between;
}

@mixin designCardLogo {
  width: 0.7rem;
  height: 0.7rem;
  background-color: transparent;
  border: 1px solid rgba(255, 86, 19, 0.74);
}

@mixin work-cards {
  width: 100vw;
  height: 50vh;
  margin-bottom: 5rem;
  display: flex;
  justify-content: center;
  font-family: Arial, Helvetica, sans-serif;
  background-color: rgb(12, 12, 12);
  color: #d3c8c8cc;

  .project {
    width: 55%;
    height: 100%;
    display: flex;
    position: relative;
    border-bottom: 2px solid #c2363d4d;
    .image {
      width: 57%;
      height: 82%;
      border-radius: 0.5rem;
      .overlay {
        height: 100%;
        width: 100%;
        background-color: #1369627e;
        transition: opacity 0.5s ease-in-out;
        border-radius: 0.5rem;
        z-index: 1;
        &:hover {
          opacity: 0;
        }
      }
    }

    .project-content {
      width: 50%;
      height: 82%;
      right: 0;
      text-align: right;
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .project-title {
        margin-top: 0;
        font-size: 2vw;
        color: #c2363dee;
      }
      .project-description {
        background-color: #181616;
        max-height: 24vh;
        padding: 3%;
        font-size: 0.9vw;
        border-radius: 5px;

        p {
          padding: 0;
          margin: 0;
          line-height: 160%;
          letter-spacing: 1.2px;
          color: #d3c8c8cc;
        }
      }
      ul {
        font-size: small;
        list-style: none;
        display: flex;
        justify-content: flex-end;
        color: #c2363dee;
        margin-top: 1rem;
        li {
          margin-left: 5%;
          font-size: 0.9vw;
        }
      }

      .iconWork {
        font-size: 1.2vw;
        &:hover {
          color: #c2363dee;
          cursor: pointer;
        }
      }
    }
  }
}

@mixin borders {
  border-width: 0.2rem;
  border-style: double;
  border-image-repeat: stretch;
  border-image-slice: 40;
  border-image-source: repeating-linear-gradient(
    15deg,
    rgba(80, 28, 35, 0.747),
    #1369627e,
    rgba(77, 26, 32, 0.747),
    rgba(252, 249, 249, 0.933)
  );
}

@mixin work-description-services-box-shadow-right {
  transition: 0.5s ease-in-out;
  -webkit-box-shadow: 3px 4px 2px 0px #201f1fbf;
  -moz-box-shadow: 3px 4px 2px 0px #201f1fbf;
  box-shadow: 3px 4px 2px 0px #201f1fbf;

  &:hover {
    transform: scale(1.05);
    transform: translate(-0.5rem, -0.5rem);
    -webkit-box-shadow: 7px 10px 5px 0px #201f1fbf;
    -moz-box-shadow: 7px 10px 5px 0px #201f1fbf;
    box-shadow: 7px 10px 5px 0px #201f1fbf;
  }
}
